import React, { useEffect } from 'react';
import axios from 'axios';

const baseURL = "https://eys.mavimor.com.tr/api/Giris/vvinosayilari";

export default function EkipmanSayi() {
  const [post, setPost] = React.useState(null);

  React.useEffect(() => {
    axios
    .post(baseURL)
    .then((response) => {
      setPost(response.data);
    })
  },[])

  if(!post) return null;
  return(
    <div>
      <h3 style={{color:"white"}} >Ekipman Sayısı: {post.ekipman}</h3>
    </div>
  )

}